.row{
    width: 100%;
    margin: 0 auto;
}
.service-box {
  background-color: #fff;
  box-shadow: 0 13px 8px -10px rgb(0 0 0 / 10%);
  text-align: center;
  padding: 30px;
  border-radius: 10px;
}
.service-box .service-icon {

  width: 100px;
  height: 100px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: 10px solid #0078ff;
  border-radius: 50%;
  padding: 1rem;
}
.service-box .service-icon i {
  font-size: 2rem;
}
.service-content .service-title {
  padding: 1rem 0;
}
.service-content .service-info {
  color: #4e4e4e;
}
